<template>
  <div>
    <NavigationBar />
    <NavigationContent>
      <section class="hero is-medium is-bold">
        <div class="hero-body">
          <div class="container">
            <h1 class="title">
              {{ project.title }}
            </h1>
            <h2 class="subtitle">
              {{ project.subtitle }}
            </h2>
            <h2 class="subtitle">
              <span
                v-bind:key="key"
                v-for="(key, value, index) in project.links"
              >
                <span v-if="index !== 0"> | </span>
                <a target="_blank" :href="value">{{ key }}</a>
              </span>
            </h2>
          </div>
        </div>
      </section>
      <section>
        <div class="container fin-work-content">
          <transition name="fade">
            <div
              class="content"
              v-if="!(!readme || 0 === readme.length)"
              v-html="readme"
            ></div>
            <div v-else>
              <p style="text-align: center">Loading...</p>
            </div>
          </transition>
        </div>
      </section>
      <section>
        <div class="container fin-work-bottom">
          <nav class="level">
            <div class="level-left">
              <div v-if="previous" class="level-item has-text-centered">
                <router-link
                  :key="$route.path"
                  :to="router.options.base + previous.url"
                >
                  <p class="heading">{{ previous.title }}</p>
                  <p class="subtitle">Previous</p>
                </router-link>
              </div>
            </div>

            <div class="level-right">
              <div v-if="next" class="level-item has-text-centered">
                <router-link
                  :key="$route.path"
                  :to="router.options.base + next.url"
                >
                  <p class="heading">{{ next.title }}</p>
                  <p class="subtitle">Next</p>
                </router-link>
              </div>
            </div>
          </nav>
        </div>
      </section>
      <Footer />
    </NavigationContent>
  </div>
</template>

<script>
import NavigationBar from "../components/NavigationBar.vue";
import Footer from "../components/Footer.vue";
import NavigationContent from "../components/NavigationContent.vue";
import axios from "axios";
import marked from "marked";
import videoJs from "video.js";
import videoJsYoutube from "videojs-youtube";
import highlightJs from "highlight.js";

export default {
  name: "Work",
  data: function() {
    return {
      readme: ""
    };
  },
  props: ["project", "projects", "indexOfProject"],
  components: {
    NavigationBar,
    Footer,
    NavigationContent
  },
  methods: {
    refreshContent() {
      const vueComponent = this;
      vueComponent.readme = "";
      const baseUrl =
        vueComponent.$router.options.base + vueComponent.project.folder + "/";
      axios
        .get(
          this.$router.options.base +
            this.project.folder +
            "/" +
            this.project.readmeFile +
            "?v=" +
            process.env.assetsBuildTimeStamp
        )
        .then(function(response) {
          vueComponent.readme = marked(response.data, {
            baseUrl: baseUrl,
            gfm: true,
            tables: true,
            highlight: function(code) {
              return highlightJs.highlightAuto(code).value;
            }
          });
        });
    },
    getTileImageLink() {
      return (
        this.router.options.base +
        this.project.folder +
        "/" +
        this.project.tileImage +
        "?v=" +
        process.env.assetsBuildTimeStamp
      );
    },
    getTitle() {
      return this.project.title + " - " + this.project.subtitle;
    },
    getDesc() {
      return "Portfolio from 郑昊天 ZHENG HAOTIAN (aka Justin Fincher / JustZht)";
    },
    getUrl() {
      return (
        window.location.protocol +
        "//" +
        window.location.hostname +
        (window.location.port ? ":" + window.location.port : "") +
        this.route.path
      );
    }
  },
  computed: {
    router: function() {
      return this.$router;
    },
    route: function() {
      return this.$route;
    },
    previous: function() {
      if (this.indexOfProject > 0) {
        return this.projects[this.indexOfProject - 1];
      } else {
        return null;
      }
    },
    next: function() {
      if (this.indexOfProject < this.projects.length - 1) {
        return this.projects[this.indexOfProject + 1];
      } else {
        return null;
      }
    }
  },
  mounted: function() {
    videoJsYoutube;
    this.refreshContent();
  },
  watch: {
    $route: function() {
      this.refreshContent();
      scroll(0, 0);
    },
    readme: function() {
      if (!(!this.readme || 0 === this.readme.length)) {
        this.$nextTick(function() {
          document.querySelectorAll(".video-js").forEach(ele => {
            videoJs(ele, JSON.parse(ele.getAttribute("data")), function() {});
          });
          document.dispatchEvent(new Event("render-event"));
        });
      }
    }
  },
  head: {
    title: function() {
      return {
        inner: this.project.title,
        separator: "-",
        complement: this.project.subtitle
      };
    },
    meta: function() {
      return [
        {
          name: "description",
          content: this.getDesc()
        },
        {
          name: "twitter:title",
          content: this.getTitle()
        },
        { name: "twitter:creator", content: "@JustZht" },
        { name: "twitter:site", content: "@JustZht" },
        {
          name: "twitter:description",
          content: this.getDesc()
        },
        { name: "twitter:card", content: "summary_large_image" },
        {
          name: "twitter:image",
          content: this.getTileImageLink()
        },
        {
          name: "twitter:image:src",
          content: this.getTileImageLink()
        },
        {
          property: "og:type",
          content: "article"
        },
        {
          property: "og:site_name",
          content: this.getDesc()
        },
        {
          property: "og:title",
          content: this.getTitle()
        },
        {
          property: "og:description",
          content: this.getDesc()
        },
        {
          property: "og:image",
          content: this.getTileImageLink()
        }
      ];
    }
  }
};
</script>

